import MButton from '../../../base/button/button'
import { mapGetters } from 'vuex'
export default {
  name: "buyCoin",
  components: {
    MButton
  },
  data() {
    return {
      btnSaveState: false,
      stateText: '未绑定',
      wechatNum: 'ghstar',
      phone: '',
    }
  },
  computed: {
    ...mapGetters([
      'userPhone',
      'userInfo'
    ])
  },
  mounted() {
    console.log(this.userPhone)
  },
  methods: {
    //绑定微信
    doWeixin() {
      this.$axios.post('/api/user/register2', {
        telephone: this.userPhone,
        wechatNum: this.wechatNum
      }).then((response) => {
        let data = response.data
        if (data.code !== "200") {
          this.$vux.alert.show({
            title: '错误提示',
            content: data.message,
          })
        } else {
          //登录成功
          this.btnSaveState = true
          this.stateText = '已绑定'
        }
      }).catch((error) => {
        this.$vux.alert.show({
          title: '错误提示',
          content: '服务器错误',
        })
      })
    },
    doNext() {
      if (this.btnSaveState) {
        this.$router.push({ name: 'registerThree' })
      }
    }
  }
}



